.product{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: inherit;
        margin:10px;
        padding:20px;
        width:100%;
        max-height:500px;
        min-width:100px;
       background-color: white;
        z-index:1;
        
    
}
.product__info{
height:100px;
margin-bottom:15px;
width:100%;


}

.product__button{
    background-color: #f0c14b;
    border:1px solid;
    margin-top:10px;
    border-color:#a88734 #9c7e32 #846a29;
    color:black;





}

.product__price{
margin-top:5px;


}

.product > img{

    max-height:200px;
    width:100%;
    object-fit:contain;
    margin-top:15px;
    margin-bottom:15px;

}

.product__rating{
    
    display: flex;
    
    
}



